import React, {useState, useContext} from 'react'
import Option from "./screw/Option.js";
import Axios from "axios";
import domain from "../../util/domain";
import UserContext from "../../context/UserContext";
import { useHistory } from "react-router-dom";

export default function SCREW() {
    
    const [navbar, setNavbar] = useState("home");
    const { user, getUser } = useContext(UserContext);
    const history = useHistory();

    
    
    async function logOut() {
        await Axios.get(`${domain}/user/logOut`);
        history.push("/login");
      };

        
    
    return (
        <><br /><br /><div className="SCREWpage">
            <div className="navButtons">
                <button className={navbar==="nachsal"?"naveachbuttonselected":"naveachbutton"} onClick={() => setNavbar("nachsal")}>"</button>
                <button className={navbar==="fud"?"naveachbuttonselected":"naveachbutton"} onClick={() => setNavbar("fud")}> </button>
                <button className={navbar==="home"?"naveachbuttonselected":"naveachbutton"} onClick={() => setNavbar("home")}>  -   </button>
                <button className={navbar==="allopinions"?"naveachbuttonselected":"naveachbutton"} onClick={() => setNavbar("allopinions")}> " </button>
                <button className={navbar==="allopinions"?"naveachbuttonselected":"naveachbutton"} onClick={() => setNavbar("allopinions")}>  </button>
                <button className="navlogout" onClick={logOut}></button>
            </div>
            <Option selected={navbar} />
        </div></>
    )
}
